<template>
    <div class="goods-details" v-if="!isFirstLoading">
        <div class="app-load row-between bg-white" v-if="isFromApp">
            <div class="row">
                <img src="@A/images/shop_logo.png" alt />
                <div class="ml10">多商户电商平台</div>
            </div>
            <button
                class="br60 bg-primary white"
                size="xs"
                @click="$getAppDownload()"
            >
                app下载
            </button>
        </div>
        <product-swiper :imgList="swiperList"></product-swiper>
        <div class="goods-info bg-white">
            <div class="count-down row-between" v-if="activity.type == 1">
                <div class="price white row">
                    <div class="mr10">
                        <price-slice
                            :showSubscript="true"
                            :price="checkedGoods.price || goodsDetail.price"
                            :weight="700"
                            first-class="first-price"
                            second-class="xxl"
                        ></price-slice>
                    </div>
                    <div class="line-through white md">
                        <price-slice
                            :showSubscript="true"
                            :price="
                                checkedGoods.market_price ||
                                goodsDetail.market_price
                            "
                        ></price-slice>
                    </div>
                </div>
                <div class="down">
                    <div class="xxs primary mb5">距秒杀结束仅剩</div>
                    <van-count-down
                        format="HH:mm:ss"
                        use-slot
                        :time="countTime"
                        @change="onChangeDate"
                        @finish="onFinished"
                    >
                        <div class="row-center">
                            <div class="item bg-primary white xs">
                                {{ timeData.hours }}
                            </div>
                            <div class="primary" style="margin: 0 6rpx">:</div>
                            <div class="item bg-primary white xs">
                                {{ timeData.minutes }}
                            </div>
                            <div class="primary" style="margin: 0 6rpx">:</div>
                            <div class="item bg-primary white xs">
                                {{ timeData.seconds }}
                            </div>
                        </div>
                    </van-count-down>
                </div>
            </div>
            <div class="info-header row" v-else>
                <div class="price row" style="flex: 1">
                    <div class="primary mr10">
                        <price-slice
                            :showSubscript="true"
                            :price="checkedGoods.price || goodsDetail.price"
                            :weight="700"
                            first-class="first-price"
                            second-class="xxl"
                        ></price-slice>
                    </div>
                    <div class="line-through muted md">
                        <price-slice
                            :showSubscript="true"
                            :price="
                                checkedGoods.market_price ||
                                goodsDetail.market_price
                            "
                        ></price-slice>
                    </div>
                </div>
                <img
                    class="icon-share"
                    @click="showShare = true"
                    src="@A/images/icon_share.png"
                />
            </div>
            <div class="row">
                <div class="goods-name lg bold" style="flex: 1">
                    {{ goodsDetail.name }}
                </div>
                <img
                    v-if="activity.type == 1"
                    class="icon-share"
                    @click="showShare = true"
                    src="@A/images/icon_share.png"
                />
            </div>
            <div class="row-between xs lighter" style="padding: 0 0.32rem 10px">
                <span v-if="goodsDetail.stock !== true"
                    >库存: {{ checkedGoods.stock || goodsDetail.stock }}件</span
                >
                <span>销量: {{ goodsDetail.sales_sum }}件</span>
                <span>浏览量: {{ goodsDetail.click_count }}件</span>
            </div>
        </div>
        <goods-like class="mt10" v-if="goodsLike.length" :lists="goodsLike" />
        <div class="details mt10 bg-white">
            <div class="title lg">商品详情</div>
            <div class="content" v-html="goodsDetail.content"></div>
        </div>
        <div class="footer row bg-white">
            <div class="btn column-center" @click="goPage('contactOffical')">
                <img class="icon" src="@A/images/icon_contact.png" />
                <span class="xxs lighter">客服</span>
            </div>
            <div
                class="btn column-center"
                hover-class="none"
                bindtap="collectGoods"
                @click="$handleCollectGoods"
            >
                <img
                    class="icon"
                    :src="
                        goodsDetail.is_collect == 0
                            ? require('@A/images/icon_collection.png')
                            : require('@A/images/detail_icon_service.png')
                    "
                />
                <span class="xxs lighter">收藏</span>
            </div>
            <div class="btn column-center" @click="goPage('cart')">
                <img
                    class="icon"
                    :class="{ shaking: iconShaking }"
                    src="@A/images/icon_cart.png"
                />
                <span class="xxs lighter">购物车</span>
                <div v-if="cartNum" class="cart-num white xxs row-center">
                    {{ cartNum }}
                </div>
            </div>
            <div
                v-if="!activity.type == 1"
                class="addcart br60 white mr10 md ml10"
                @click="onShowSpec(1)"
            >
                加入购物车
            </div>
            <div class="right-buy br60 white mr10 md" @click="onShowSpec(2)">
                立即购买
            </div>
        </div>
        <goods-spec
            :show="showSpec"
            :show-add="popupType == 1"
            :show-buy="popupType == 2"
            :goods="goodsDetail"
            @close="showSpec = false"
            @change="onChangeSpec"
            @addcart="onAddCart"
            @buynow="onBuyNow"
        />
        <transition @after-leave="afterLeave" :duration="800" @after-enter="afterEnter" >
            <div v-show="showAnim" class="goods-ball" ref="goodsBall">
                <img class="inner-ball" :src="goodsDetail.image" />
            </div>
        </transition>
        <!-- 分享 -->
        <van-action-sheet
            class="share-popup"
            v-model="showShare"
            cancel-text="取消"
        >
            <div class="title row-center bold xl">分享至</div>
            <div class="row-around share-tab">
                <div class="column-center" @click="$getPoster">
                    <img class="mb5" src="@A/images/share_preserveImg.png" />
                    生成海报
                </div>
                <div class="column-center" @click="shareWx">
                    <img class="mb5" src="@A/images/share_WeChat.png" />
                    微信好友
                </div>
                <div class="column-center" @click="shareWx">
                    <img class="mb5" src="@A/images/share_freCircle.png" />
                    朋友圈
                </div>
            </div>
        </van-action-sheet>
        <van-popup
            class="guidespopup"
            v-model="guidespopup"
            lazy-render
            position="top"
        >
            <img src="@A/images/shring_arrow.png" alt="箭头" />
            <div>
                <p class="bold lg">立即分享给好友吧</p>
                <p class="sm mt10">点击屏幕右上角将本页面分享给好友</p>
            </div>
        </van-popup>
        <van-popup
            v-model="posterpopup"
            position="center"
            closeable
            class="code-popup"
            transition="fade"
        >
            <div class="content mb10">
                <img :src="shareImg" alt class="share-img" />
            </div>
            <div class="bg-primary row-center white save-btn">
                长按保存图片到相册
            </div>
        </van-popup>
    </div>
</template>

<script>
import Vue from "vue";
import ProductSwiper from "@C/ProductSwiper";
import PriceSlice from "@C/PriceSlice";
import GoodsSpec from "@C/GoodsSpec";
import GoodsLike from "@C/GoodsLike";
import {
    getGoodsDetail,
    addCart,
    getPoster,
} from "@/api/store";
import { handleCollectGoods } from "@API/user";
import { getAppDownload } from "@API/app";
import { appSource } from "@U/utils";
import { mapState, mapActions } from "vuex";
const CollectType = {
    COLLECTION: 1,
    CANCEL_COLLECTION: 0,
};
export default {
    name: "goodsDetail",
    components: {
        ProductSwiper,
        PriceSlice,
        GoodsSpec,
        GoodsLike,
    },
    data() {
        return {
            posterpopup: false,
            shareImg: "",
            guidespopup: false,
            showShare: false,
            timeData: {},
            swiperList: [],
            goodsDetail: {},
            checkedGoods: {},
            goodsLike: [],
            isFirstLoading: true,
            showSpec: false,
            popupType: "",
            countTime: 0,
            activity: {},
            isFromApp: this.$route.query.isapp,
            showAnim: false,
            iconShaking: false,
        };
    },

    async created() {
        this.id = this.$route.query.id;
        this.$getGoodsDetail();
        this.getCartNum()
    },
    mounted() {},
    beforeDestroy() {
        clearTimeout(this.timer1);
        clearTimeout(this.timer2);
    },
    methods: {
        ...mapActions(['getCartNum']),
        $getAppDownload() {
            getAppDownload({ client: appSource() == "ios" ? 3 : 4 }).then(
                (res) => {
                    if (res.code == 1) {
                        window.location = res.data.line;
                    }
                }
            );
        },
        shareWx() {
            this.showShare = false;
            this.guidespopup = true;
        },
        onFinished() {
            this.$toast({ message: "当前秒杀已结束" });
            this.$router.go(0);
        },
        onChangeDate(e) {
            let timeData = {};
            for (let prop in e) {
                if (prop !== "milliseconds")
                    timeData[prop] = ("0" + e[prop]).slice(-2);
            }
            this.timeData = timeData;
        },
        goPage(name, query) {
            this.$router.push({
                name,
                query,
            });
        },
        
        $getGoodsDetail() {
            getGoodsDetail({
                id: this.id,
            }).then((res) => {
                if (res.code == 1) {
                    let {
                        goods_image,
                        content,
                        like,
                        activity,
                    } = res.data;
                    this.goodsDetail = res.data;
                    this.swiperList = goods_image;
                    this.goodsLike = like;
                    this.countTime = activity.info
                        ? activity.info.end_time * 1000 - Date.now()
                        : 0;
                    this.activity = activity;
                    this.isFirstLoading = false;
                } else {
                    setTimeout(() => {
                        this.$router.go(-1);
                    }, 500);
                }
            });
        },
        $getPoster() {
            this.$toast({
                type: "loading",
                message: "正在生成中...",
                duration: 0,
            });
            getPoster({
                id: this.id,
                url: `/mobile/common/goodsdetail?id=${this.id}`,
            }).then((res) => {
                this.$toast.clear();
                if (res.code == 1) {
                    this.shareImg = res.data;
                    this.showShare = false;
                    this.posterpopup = true;
                }
            });
        },
        $handleCollectGoods() {
            let { is_collect, id } = this.goodsDetail;
            if (is_collect == CollectType.COLLECTION) {
                is_collect = CollectType.CANCEL_COLLECTION;
            } else {
                is_collect = CollectType.COLLECTION;
            }
            handleCollectGoods({ is_collect: is_collect, goods_id: id }).then(
                (res) => {
                    if (res.code == 1) {
                          if(is_collect == 1) {
                             this.$toast({ message: "收藏成功"});
                        }else {
                              this.$toast({ message: "取消收藏" });
                        }
                        this.$getGoodsDetail();
                    }
                }
            );
        },
        onShowSpec(type) {
            this.popupType = type;
            this.showSpec = true;
        },
        onChangeSpec(e) {
            this.checkedGoods = e;
        },
        onAddCart(e) {
            let { id, goodsNum } = e;
            addCart({
                item_id: id,
                goods_num: goodsNum,
            }).then((res) => {
                if (res.code == 1) {
                    this.iconShaking = false;
                    this.showSpec = false;
                    this.onControlAnim();
                    this.getCartNum()
                }
            });
        },
        onBuyNow(e) {
            let { id, goodsNum } = e;
            let goods = [
                {
                    item_id: id,
                    num: goodsNum,
                },
            ];
            this.showSpec = false;
            this.$nextTick(() => {
                this.$router.push({
                    name: "confirmOrder",
                    query: {
                        goods: JSON.stringify(goods),
                    },
                });
            });
        },
        // 控制动画方法
        onControlAnim() {
            const goodsBall = this.$refs.goodsBall;
            const innerBall = goodsBall.children[0];
            this.showAnim = true;
        },
        afterEnter(el) {
            this.showAnim = false;
        },
        afterLeave(el) {
            // 动画结束后
            this.iconShaking = true;
        },
    },
    computed: {
        ...mapState(['cartNum'])
    },
};
</script>
